<template>
  <div class>
    <h1>{{ id ? '编辑':'新建'}}个人作品</h1>
    <el-form @submit.native.prevent="save">
        <el-form-item label="标题" label-width="120px">
          <el-input v-model="model.title"></el-input>
        </el-form-item>

         <el-form-item label="详情" label-width="120px">
        <vue-editor id="editor" useCustomImageHandler @image-added="handleImageAdded" v-model="model.body"> </vue-editor>
      </el-form-item>

      <el-form-item style="margin-top:1rem;">
        <el-button type="primary" native-type="submit">保存</el-button>
        <el-button @click="$router.push('/works/list')">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

import { VueEditor } from "vue2-editor";
export default {
   components: {
    VueEditor
  },
  props: {
    id: {}
  },
  data() {
    return {
      model: {
        items: []
      }
    };
  },
  created() {
    this.id && this.fetch();
  },
  methods: {
    // 富文本编译器本地图片上传
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
          const formData = new FormData();
          formData.append("file", file);
          const res=await this.$http.post('upload',formData)
          const url = res.data.url; // Get url from response
          Editor.insertEmbed(cursorLocation, "image", url);
          resetUploader();
        },
    async save() {
      //换成同步
      let res;
      if (this.id) {
        res = await this.$http.put(`rest/works/${this.id}`, this.model);
      } else {
        res = await this.$http.post("rest/works", this.model);
      }

      console.log(res);
      if (res.status == 200) {
        this.$router.push("/works/list");
        this.$message({
          type: "success",
          message: "保存成功"
        });
      }
    },

    async fetch() {
      const res = await this.$http.get(`rest/works/${this.id}`);
      if (res.status == 200) {
        this.model = Object.assign({}, this.model, res.data);
      }
    }
  }
};
</script>

