<template>
  <div class>
    <h1>查看留言</h1>
    <el-form @submit.native.prevent="save">
        <el-form-item label="姓名" label-width="120px">
          <el-input v-model="model.username" readonly></el-input>
        </el-form-item>
        <el-form-item label="电话" label-width="120px">
          <el-input v-model="model.mobile" readonly></el-input>
        </el-form-item>
        <el-form-item label="邮箱" label-width="120px">
          <el-input v-model="model.email" readonly></el-input>
        </el-form-item>
         <el-form-item label="详情" label-width="120px">
           <el-input type="textarea" v-model="model.body" readonly></el-input>
         </el-form-item>

      <el-form-item style="margin-top:1rem;">
        <!-- <el-button type="primary" native-type="submit">保存</el-button> -->
        <el-button @click="$router.push('/blog/list')">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {
        items: []
      }
    };
  },
  created() {
    this.id && this.fetch();
  },
  methods: {
    async save() {
      //换成同步
      let res;
      if (this.id) {
        res = await this.$http.put(`rest/blog/${this.id}`, this.model);
      } else {
        res = await this.$http.post("rest/blog", this.model);
      }

      console.log(res);
      if (res.status == 200) {
        this.$router.push("/blog/list");
        this.$message({
          type: "success",
          message: "保存成功"
        });
      }
    },

    async fetch() {
      const res = await this.$http.get(`rest/blog/${this.id}`);
      if (res.status == 200) {
        this.model = Object.assign({}, this.model, res.data);
      }
    }
  }
};
</script>

