import Vue from 'vue'
import VueRouter from 'vue-router'

import Main from '../views/Main.vue'
import Login from '../views/Login.vue'

import AdvEdit from '../views/AdEdit.vue'
import AdvList from '../views/AdList.vue'

import NoteEdit from '../views/NoteEdit.vue'
import NoteList from '../views/NoteList.vue'

import MemoryEdit from '../views/MemoryEdit.vue'
import MemoryList from '../views/MemoryList.vue'

import WorkEdit from '../views/WorkEdit.vue'
import WorkList from '../views/WorkList.vue'

import BlogEdit from '../views/BlogEdit.vue'
import BlogList from '../views/BlogList.vue'

import AdminUserEdit from '../views/AdminUserEdit.vue'
import AdminUserList from '../views/AdminUserList.vue'

import About from '../views/About'

Vue.use(VueRouter)

  const routes = [
    { path: '/login',name:'login' , component: Login,meta: { isPubile:true } },       //是否公开
    {
      path: '/',
      name: 'Main',
      component: Main,  
      children: [
          { path: '/ads/create', name: 'AdEdit', component: AdvEdit,},
          { path: '/ads/edit/:id', name: 'AdEdit', component: AdvEdit,props: true},
          { path: '/ads/list', name: 'AdList', component: AdvList,},

          { path: '/notes/create', name: 'NoteEdit', component: NoteEdit,},
          { path: '/notes/edit/:id', name: 'NoteEdit', component: NoteEdit,props: true},
          { path: '/notes/list', name: 'NoteList', component: NoteList,},

          { path: '/memories/create', name: 'MemoryEdit', component: MemoryEdit,},
          { path: '/memories/edit/:id', name: 'MemoryEdit', component: MemoryEdit,props: true},
          { path: '/memories/list', name: 'MemoryList', component: MemoryList,},

          { path: '/works/create', name: 'WorkEdit', component: WorkEdit,},
          { path: '/works/edit/:id', name: 'WorkEdit', component: WorkEdit,props: true},
          { path: '/works/list', name: 'WorkList', component: WorkList,},
  
          { path: '/blog/create', name: 'BlogEdit', component: BlogEdit,},
          { path: '/blog/edit/:id', name: 'BlogEdit', component: BlogEdit,props: true},
          { path: '/blog/list', name: 'BlogList', component: BlogList,},

          { path: '/admin_user/create', name: 'AdminUserEdit', component: AdminUserEdit,},
          { path: '/admin_user/edit/:id', name: 'AdminUserEdit', component: AdminUserEdit,props: true},
          { path: '/admin_user/list', name: 'AdminUserList', component: AdminUserList,},

          { path: '/about', name: 'About', component: About,},
        ]
  
    },
]

// 在vue里面全局注入
Vue.mixin({
  computed:{
    uploadURl(){
      return this.$http.defaults.baseURL+'/upload'
    }
  },
  methods:{
    getAuthHeaders(){
      return{
        Authorization:`Bearer ${localStorage.token || ''}  `
      }
    }
  }
})

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if(!to.meta.isPubile && !localStorage.token){
  console.log('need login')
  return next('/login');
  }
  next()
})

export default router
