<template>
  <div class="">
    <h1>{{ id ? '编辑':'新建'}}管理员</h1>
     <el-form @submit.native.prevent='save'>
      <el-form-item label="用户名" label-width="120px">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
       <el-form-item label="密码" label-width="120px">
        <el-input type="password" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
        <el-button @click="$router.push('/admin_user/list')">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
    props: {
      id:{}
    },
    data () {
        return {
            model:{},
        }
    },
    created () {
      this.id && this.fetch()
    },
    methods: {
        async save(){     //换成同步
            let res
            if(this.id){
               res=await this.$http.put(`rest/admin_user/${this.id}`,this.model)               
            }else{
              res=await this.$http.post('rest/admin_user',this.model)
            }
            
            console.log(res)
            if(res.status==200){
            this.$router.push('/admin_user/list')
            this.$message({
              type:"success",
              message:"保存成功"
            })
            }
        },
         
        async fetch(){
          const res=await this.$http.get(`rest/admin_user/${this.id}`)
          if(res.status==200){
              this.model=res.data;
            }
        }
    }
}
</script>
