<template>
  <div class="">
    <h1>留言列表</h1>
   <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="_id"
        label="ID"
        width="">
      </el-table-column>
      
      <el-table-column
        prop="username"
        label="姓名"
        width="">
      </el-table-column>
       <el-table-column
        prop="mobile"
        label="电话"
        width="">
      </el-table-column>
      <el-table-column
        prop="email"
        label="邮箱"
        width="">
      </el-table-column>
     <el-table-column
        prop="updated"
        label="时间"
        width="">
      </el-table-column>
      <el-table-column
      fixed="right"
      label="操作"
      width="">
      <template slot-scope="scope">
        <el-button type="primary" @click="$router.push(`/blog/edit/${scope.row._id}`)" size="small">查看详情</el-button>
        <el-button type="primary" @click="remove(scope.row)" size="small">删除</el-button>
      </template>
    </el-table-column>

    </el-table>
    
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData:[],
    }
  },
  created () {
    this.fetch()
  },
  methods: {
    async fetch(){
      const res = await this.$http.get('rest/blog')
      
      if(res.status==200){
        this.tableData=res.data
      }
    },
     formats(utcTime){
       console.log(utcTime)
      return  utcTime.replace(/T/, ' ').replace(/\..+/, '');
    },
    async remove(row){
      console.log(row._id)
      this.$confirm(`是否要删除 '${row.username}' 的留言`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          const res = await this.$http.delete(`rest/blog/${row._id}`)
          console.log(res)
          if(res.status==200)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        })
      
    }, 
  }
}
</script>
